/* Hamburger
==================== */
#hamburger-container {
  opacity: .9;
  height: 50px;
  position: fixed;
  top: 0;
  font-size: 1.7em;
  line-height: 1.8em;
  background: #f9f8f6;
  transition: all 300ms;
  z-index: 11;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
}

#hamburger-container #hamburger {
  width: 26px;
  height: 33px;
  position: absolute;
  color: #313131;
  top: 50%;
  margin-top: -17px;
  display: block;
}

#hamburger-container #hamburger .lines {
  width: 26px;
  height: 4px;
  left: 0px;
  background: #000;
  position: absolute;
  border-radius: 5px;
}

#hamburger-container #hamburger .line1 {
  top: 8px;
}

#hamburger-container #hamburger .line2 {
  top: 16px;
}

#hamburger-container #hamburger .line3 {
  top: 24px;
}

.hamburger-up {
  width: 100%;
}

.hamburger-up #hamburger {
  left: 15px;
}

.hamburger-down {
  width: 1.8em;
  position: fixed;
  margin: .5em;
  border-radius: 3px;
  border: solid 1px #b8b8b8;
}

.hamburger-down #hamburger {
  left: 11px;
}

.hamburger:hover {
  background: #444;
}

.top-nav {
  width: 100%;
  background: #fff;
  position: fixed;
  transition: top 400ms;
  top: -25em;
  z-index: 11;
  border-bottom: 3px solid #E9DFE9;
}

.top-nav ul {
  text-align: center;
  padding: 1em 0;
}

.top-nav li a {
  width: 100%;
  display: inline-block;
  padding: .6em 0;
  text-transform: uppercase;
  transition: color 200ms;
  border-radius: 6px;
  color: #000;
  text-decoration: none;
}

.top-nav li a:hover {
  color: #d67d7d;
}

.top-nav .close-top-nav {
  display: inline-block;
}

@media (min-width: 768px) {
  #hamburger-container {
    display: none;
  }
  .top-nav {
    position: initial;
    width: 100%;
    height: 3.2em;
    margin: 0;
    top: 0em;
    border: none;
  }
  .top-nav ul {
